  left: 0;
  justify-content: center;
  width: 100%;
  height: 64px;
  transition: transform 200ms cubic-bezier(0.4, 0.01, 0.165, 0.99);
  border-bottom: 1px solid #E6E8EB;
  background-color: #FFF;
