.navbar-dropdown[data-v-502f4500] {
  visibility: hidden;
  position: absolute;
  right: 0;
  width: 360px;
  margin-top: 18px;
  transition: all 0.25s linear;
  border-radius: 4px;
  opacity: 0;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.16);
  pointer-events: none;
}
.navbar-dropdown[data-v-502f4500]::before {
    content: "";
    position: absolute;
    right: 0;
    width: 20px;
    height: 20px;
    transform: rotate(45deg) translateX(-50%);
    transform-origin: center center;
    border-radius: 4px;
    background-color: #FFF;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.16);
}
.navbar-dropdown.is-open[data-v-502f4500] {
    visibility: visible;
    opacity: 1;
    pointer-events: initial;
}
