.feedback[data-v-090c0eba] {
  display: flex;
  width: 320px;
  padding: 16px;
  border-radius: 4px;
  background-color: #FFF;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.16);
}
.feedback[data-v-090c0eba]:hover {
    text-decoration: none;
}
.feedback[data-v-090c0eba]::before {
    content: "";
    position: absolute;
    top: 2px;
    right: 0;
    width: 20px;
    height: 20px;
    transform: rotate(45deg) translateX(-50%);
    transform-origin: center center;
    border-radius: 4px;
    background-color: #FFF;
}
.feedback__icon[data-v-090c0eba] {
  position: relative;
  width: 48px;
  margin-right: 16px;
}
.feedback__icon[data-v-090c0eba]::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: #047AE6;
}
.feedback__icon[data-v-090c0eba]::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 48px;
    background-image: url(/assets/1.0.0-assets.136/images/chat-bubble.svg);
    background-repeat: no-repeat;
    background-position: center;
}
.feedback__message[data-v-090c0eba] {
  flex: 1;
  color: #2E3C43;
}
.feedback__paragraph[data-v-090c0eba] {
  margin-bottom: 8px;
}
.feedback__paragraph[data-v-090c0eba]:last-child {
