  text-align: center;
  white-space: nowrap; }
  .hangar .button.is-outline {
    padding: 14px 22px; }
  .hangar .button:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: transparent;
    transition: background-color 0.1s linear; }
  .hangar .button span {
    position: relative;
    display: inline-flex;
    overflow: hidden; }
    .hangar .button span:before {
      content: '';
      position: absolute;
      transition: transform .3s ease;
      left: -1px;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: currentColor;
      transform: translateX(-100%); }
  .hangar .button:hover, .hangar .button:focus {
    text-decoration: none; }
    .hangar .button:hover span:before, .hangar .button:focus span:before {
      transform: translateX(0); }
  .hangar .button > span {
    position: relative; }
  .hangar .button.is-cartoframes {
    background-color: #3D33CC;
    color: #FFF; }
    .hangar .button.is-cartoframes.is-outline {
