.card[data-v-606a129e] {
  position: relative;
  height: 100%;
  padding: 16px;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid #EBEEF5;
  background-color: #FFF;
}
.card[data-v-606a129e]:hover {
    border-color: transparent;
    box-shadow: 0 8px 20px 0 rgba(194, 205, 214, 0.8);
    cursor: pointer;
}
.card:hover .tag__title[data-v-606a129e] {
      color: #047AE6;
      text-decoration: underline;
}
.card.condensed[data-v-606a129e]:hover {
    background-color: #F2F6F9;
    box-shadow: none;
}
.tag__title[data-v-606a129e] {
  overflow: hidden;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
  text-overflow: ellipsis;
  white-space: nowrap;
}
.tag__title[data-v-606a129e],
.tag__count[data-v-606a129e] {
  position: relative;
  margin-bottom: 8px;
}
.tag__title[data-v-606a129e]::before,
  .tag__count[data-v-606a129e]::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 12px;
    height: 100%;
    transform: translate3d(0, -50%, 0);
    background-repeat: no-repeat;
    background-position: center left;
    background-size: contain;
}
.tag__title[data-v-606a129e]:last-child,
  .tag__count[data-v-606a129e]:last-child {
    margin-bottom: 0;
}
.tag__count[data-v-606a129e] {
  padding-left: 24px;
}
.link[data-v-606a129e]:hover {
  text-decoration: none;
}
.tag__count--maps[data-v-606a129e]::before {
  width: 15px;
  background-image: url(/assets/1.0.0-assets.136/images/map.svg);
}
.tag__count--datasets[data-v-606a129e]::before {
